import Header from "./components/Header";
import {Routes,Route} from 'react-router-dom'
import Home from "./pages/Home";
import AllProducts from "./pages/AllProducts";
import Login from "./pages/Login";
import Panel from "./pages/Panel";
import Shopcart from "./pages/Shopcart";
import { useEffect,useState } from "react";
import axios from 'axios';
import { useNavigate } from "react-router-dom";
import {useDispatch } from "react-redux";
import Wishlist from "./pages/Wishlist";
import Footer from "./components/Footer";
import ScrollToTop from "./components/ScrollTop";
import HashLoader  from "react-spinners/HashLoader";
import { css } from "@emotion/react";

function App() {
  let dispatch=useDispatch();
  let navigate=useNavigate();

  const override = css`
  display: block;
  margin: 0 auto;
  border-color: red;
`;
const [loading, setLoading] = useState(false);
const [width, Setwidth] = useState();
  useEffect(()=>{
    setLoading(true)
    axios.get("https://my-json-server.typicode.com/T-Ulvi/DB/db").then(res=>res.data)
    .then(res=>dispatch({type:"All_Data",payload:res.products}))
    .then(res=> setLoading(false))
    navigate("/TimeZone");
  Setwidth(window.innerWidth);
  },[]);

  return (
   <>
    <div className={loading? "sweet-loading position-absolute h-100 w-100 border bg-light":"d-none"}>
         <HashLoader  color={"#ffea00"} loading={loading} css={override} size={width>426? 400:120} />
      </div>
    <div className={loading? "d-none":"App"}>
      <Header />
        <ScrollToTop>
        <Routes>
          <Route path="/TimeZone" element={<Home/>}/>
          <Route path="/allProducts" element={<AllProducts/>}/>
          <Route path="/login" element={<Login/>}/>
          <Route path="/panel" element={<Panel/>}/>
          <Route path="/wishlist" element={<Wishlist/>}/>
          <Route path="/shopcart" element={<Shopcart/>}/>
        </Routes>
        </ScrollToTop>
        <Footer/>
    </div>
   </>
  );
}

export default App;
